import React from "react";

const EnrolForm = () => {
  return (
    <>
      <section class="enrolform">
        <div class="container">
          <div class="">
            <form>
              <div class="row">
                <div class="col-md-6">
                  <div class="input-group mb-4">
                  
                    <input
                      type="text"
                      class="form-control"
                      id="inlineFormInputGroup"
                      placeholder="FIRST NAME"
                    />
                  </div>
                </div>
                <div class="col-md-6">
                  <div class="input-group mb-4">
                    <input
                      type="text"
                      class="form-control"
                      id="inlineFormInputGroup"
                      placeholder="LAST NAME"
                    />
                  </div>
                </div>
                 <div class="col-md-12">
                  <div class="input-group mb-4">
                    <input
                      type="text"
                      class="form-control"
                      id="inlineFormInputGroup"
                      placeholder="LOCATION"
                    />
                  </div>
                </div>

                  <div class="col-md-12">
                  <div class="input-group mb-4">
                    <input
                      type="text"
                      class="form-control"
                      id="inlineFormInputGroup"
                      placeholder="PHONE NUMBER"
                    />
                  </div>
                </div>
                  <div class="col-md-12">
                  <div class="input-group mb-4">
                    <input
                      type="text"
                      class="form-control"
                      id="inlineFormInputGroup"
                      placeholder="EMAIL"
                    />
                  </div>
                </div>

                  <div class="col-md-12">
                  <div class="input-group mb-4">
                   <textarea id="" name="" rows="4"
                    placeholder="MESSAGE" cols="100">
                   </textarea>
                  </div>
                </div>


                
                
              </div>

              <button type="button" class="btn btn-light px-5  shadow">
                {" "}
                <strong>SUBMIT </strong>{" "}
              </button>
            </form>
          </div>
        </div>
      </section>
    </>
  );
};

export default EnrolForm;
